@use 'common.scss';

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'VeeWithAMarker';/*'VeeWithAMarker';*/
}

body {
  font-family: Arial, sans-serif;
  background: radial-gradient(circle, rgba(105,136,208,1) 0%, rgba(105,136,208,1) 65%, rgb(129, 151, 202) 100%); 
  background-color: common.$background;
  color: #f5f2ef;
}

.home-sun {
  /* center image */
  display: block;
  max-width: 100vw;
  max-height: 35vh;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
}

#cloud-container, #fg-cloud-container, #bg-cloud-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh; 
  overflow: hidden;
  pointer-events: none;
  user-select: none; 
}

#fg-cloud-container {
  z-index: 5;
  filter: hue-rotate(-25deg) contrast(1) opacity(0.4);
}

#bg-cloud-container {
  z-index: 3;
  filter: hue-rotate(25deg) contrast(0.6) opacity(0.2);
}

.home {
  .writing .hero {
    display: none;
    height: 0;
  }
  .writing {
    backdrop-filter: blur(10px);
  }
}

.home-padding {
  width: 100vw;
  height: 65vh;
  z-index: 55;
}

section {
  margin-top: 32px;
  padding-bottom: 8px;
}

/* Basic layout styles */
.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

main {
  flex-grow: 1;
  padding: 20px;
}

/* Navbar styles */
.navigation, .nav-toggle {
  color: common.$accent;
  padding: 10px;
  position: fixed;
  z-index: 100;
  letter-spacing: 2px;

  right: 20px;

  @media screen and (min-width: 720px) {
    left:unset
  }
}

.navigation {
  bottom: 10vh;

  @media screen and (min-width: 720px) {
    left: 6vw;
    right: unset;
  }
}

.nav-toggle {
  bottom: 2vh;
  user-select: none;
  font-size: 2rem;
  border: 2px solid common.$accent;
  backdrop-filter: blur(4px);

  @media screen and (min-width: 720px) {
    left: 120px;
    right: unset;
    
    &:hover {
      cursor: pointer;
      font-size: 2.6rem;
      color: common.$accent;
      border-color: common.$accent;
      box-shadow: 0 0 5px common.$accent;
  }
  }
}

nav ul {
  list-style: none;
  display: block;
  backdrop-filter: blur(4px);
  border-radius: 16px;
  padding: 8px;
}

nav ul li {
  margin-right: 10px;

  &:before {
    content: '>';
    margin-right: 10px;
  }
}

nav ul li a {
  color: common.$accent;
  font-size: 3rem;
  text-decoration: none;

  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}

nav ul li a:hover {
  font-size: 4rem;
  color: common.$accent;
}

/* Footer */
.footer, .footer-space {
  position: relative;
  z-index: -10;
  min-height: 100vh;
  user-select: none;
  pointer-events: none;
}

/* Hero section styles */
.hero-sun {
  width: 100px;
  margin-top: 12vh;
  /* center and position fixed */
  position: fixed;
  z-index: -1;
  left: calc(50vw - (100px / 2));
  right: auto;

  @media screen and (min-width: 480px) {
    margin-top: 10vh;
  }
}

.hero-link {
  position: relative;
  z-index: 15;
}

.hero {
  text-align: center;
  user-select: none;
  pointer-events: all;
  width: 100vw;
  z-index: 22;
  min-height: 22vh;
  max-height: fit-content;

  @media screen and (min-width: 480px) {
    min-height: 18vh;
  }

  h1, p {
        position: fixed;
        left: auto;
        right: auto;
  }

  h1 {
      padding-top: 8px;
      padding-bottom: 32px;
      margin: 0;
      color: common.$accent;
      letter-spacing: 2px;
      width: 100vw;
      top: 2vh;
      font-size: 2.4rem; 

      @media screen and (min-width: 320px) {
        font-size: 3.4rem; 
      }

      @media screen and (min-width: 480px) {
        font-size: 4rem;
      }
  }

  p {
      padding-top: 1vh;
      margin-top: 56px;
      width: 100vw;
      letter-spacing: 2px;
      color: #e4ddd5;
      top: 2.1vh;

      @media screen and (min-width: 480px) {
      }
    }
}

.hero-with-sun {
  min-height: 15vh;
}

/* About me section styles */
.about-me {
  margin-bottom: 30px;
}

/* Skills section styles */
.skills ul {
  list-style: none;
}

/* Article Card */
.articles-container-container, .projects-sections {
  padding-bottom: 0px;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
}

.page-title {
  margin-left: 10%;
}

main {
  backdrop-filter: blur(10px);
}

.articles-section h2, .projects-sections h2 {
  margin-bottom: 8px;
  margin-top: -32px;
  padding-top: 32px;
  margin-left: 10%;
  user-select: none;

}

.articles-container, .projects-container {
  margin-left: 10%;
  padding-bottom: 16px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(333px, 1fr));
  gap: 8px;

  div {
    p,a  {
      color: common.$primary-text;
      text-decoration: none;
    }
  }

  .promo-card_headline {
    font-size: 1.6rem;
    letter-spacing: normal;
  }

  .promo-card_summary {
    font-size: 1.2rem;
    font-family: 'LouisGeorgeCafe';
    letter-spacing: 0px;
  }

  .promo-card_content {
    padding-left: 0;
    padding-bottom: 48px;
    font-family: 'LouisGeorgeCafe';

    @media screen and (min-width: 720px) {
      padding-bottom: 8px;
    }
  }


  .promo-card_read-more {
    text-decoration: underline;
    font-size: 1.2rem;

    &:hover {
      transform:scale(1.6);
      border: none;
    }
  }

  img {
    max-width: 300px;
    height: auto;
  }
}

.projects-sections {
  backdrop-filter: blur(10px);
}

.projects-section__title {
  margin-bottom: 8px;
}

.PortfolioItem {
  max-width: 350px;
  margin-bottom: 16px;
}

.PortfolioItem__title {
  font-size: 1.4rem;
  letter-spacing: 1px;
}

.PortfolioItem__status {
  margin-bottom: 16px;
}

.PortfolioItem__description {
  font-family: 'LouisGeorgeCafe';
  font-size: 1.2rem;
  letter-spacing: -1px;
  margin-bottom: 16px;
}

.PortfolioItem__link-button {
  float: left;
  padding: 16px; 
  text-align: center;
  font-size: 1rem;
  box-shadow: 0 0 2px #e4ddd5;

  &:hover {
    cursor: pointer;
    color: common.$accent;
    box-shadow: 0 0 5px common.$accent;
    text-decoration: underline;
  }

}

.PortfolioItem__date {
  float: right;
}

.PortfolioItem__tech {
  font-size: 0.9rem;
  
  margin-bottom: 8px;

  &:before {
    content: "::";
    font-size: 1rem;
  }

  &:after {
    content: "::";
  }
}
